import TabBar from '@/components/common/TabBar.vue'
import Lines from '@/components/common/Lines.vue'
import $http from '@/common/api/request.js'
export default {
	components: {
		Lines,
		TabBar
	},
	data() {
		return {
			clientHeight: 0,
			currentIndex: 0,
			// 左侧的数据源
			leftData: [],
			// 右侧的数据源
			rightData: []
		}
	},
	onReady() {
		// 获取内容可视区域的高度 
		uni.getSystemInfo({
			success: (res) => {
				this.clientHeight = res.windowHeight - this.getsystmClientHeight()
				// this.clientHeight = 2000	
				// this.clientHeight = res.windowHeight - uni.upx2px(80)
			}
		})
	},
	// 监听原生标题栏搜索输入框点击事件
	onNavigationBarSearchInputClicked() {
		uni.navigateTo({
			url: `/pages/Search/Search`
		})
	},
	onLoad() {
		this.getData()
	},
	methods: {
		// 请求数据
		getData(id) {
			if (id === (this.currentIndex + 1)) {
				return;
			}
			// 请求数据源
			$http.request({
				url: '/goods/list'
			}).then(res => {
				let leftData = []
				let rightData = []
				let that = this
				res.forEach(item => {
					leftData.push({
						id: item.id,
						name: item.name
					})
					// 如果id相同就加载右侧的数据  为什么我这边的的数据判断有问题
					let idx = that.currentIndex + 1
					
						that.rightData = item.data
				})
				this.leftData = leftData
				
			}).catch(() => {
				uni.showToast({
					title: "请求失败",
					icon: "none"
				})
			})
		},

		// 获取手机不同设备的可视高度（ios，android）兼容(但是我这里有bug没有用)  这里的数据没有用
		// 获取到的手机状态栏上的信息不准确
		getsystmClientHeight() {
			const res = uni.getSystemInfoSync()
			// console.log(res)
			// console.log(res.platform,res.statusBarHeight)
			const systme = res.platform
			if (systme === 'android') {
				return -28 + res.statusBarHeight
			} else if (systme === 'ios') {
				return 44 + res.statusBarHeight
			} else {
				return
			}
		},
		// 监听左侧点击事件
		ChangeIndex(index, id) {
			this.getData(id)
			this.currentIndex = index
		}
	}
}
